/**
 * SCSS styles for ModalsPortal.tsx
 */
.ModalsPortal__wrapper {
    position: fixed;
    flex-direction: column;
    justify-content: center;
    top: 0;
    left: 0;
    z-index: 1000;
    width: 100%;
    height: 100%;
    display: flex;
    visibility: hidden;
    opacity: 0;
    transition: all 0.2s linear;
}

.ModalsPortal__wrapper.open {
    visibility: visible;
    opacity: 1;
}

.ModalsPortal__backdrop {
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.5);
}

.ModalsPortal__container {
    position: relative;
    width: 100%;
    max-height: 80%;
    background-clip: border-box;
    background-color: white;
    border: 4px solid white;
    box-shadow: 0 0.25rem 0.5rem rgb(0 0 0 / 50%);
    border-radius: 8px;
    padding: 12px;
    outline: 0;
    overflow: auto;
}
